import moment from '@nextcloud/moment';
export default {
  name: 'Confirmation',
  props: {
    booking: {
      required: true,
      type: Object
    }
  },
  computed: {
    startDate() {
      return moment(this.booking.start * 1000).format('LLL');
    },
    endDate() {
      return moment(this.booking.end * 1000).format('LLL');
    }
  }
};